import React, { Component } from "react";
import styled from "styled-components";
import Aos from "aos";
import "aos/dist/aos.css";
import Paragraph from "../theme/paragraph";
import { HeartFillIcon } from "@primer/octicons-react";

const MoviePoster = styled.div`
  width: 200px;
  height: 300px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  border-radius: 15px;
  transition: 0.2s !important;

  :hover {
  }
`;

const Styles = styled.div`
  /* max-width: 400px; */
  max-width: 90%;
  padding-top: 70px;

  .title {
    float: left;
    text-align: left;
    font-weight: 600;
    margin-top: 10px;
    display: block;
  }

  .nominate {
    opacity: 0;
    transition: 0.2s !important;
  }

  .year {
    font-size: 18px;
    font-weight: 400;
    display: block;
  }

  .greyOut {
    background-color: red !important;
  }

  .selectPoster :hover {
    cursor: pointer;
    background: rgba(26, 51, 65, 0.73) !important;

    .nominate {
      opacity: 1;
    }
  }
`;

class MovieResult extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: this.props.title,
      poster: this.props.poster,
      year: this.props.year,
    };
  }

  componentDidMount() {
    Aos.init({
      once: true,
    });
  }

  renderMoviePoster() {
    const { poster } = this.state;
    return (
      <MoviePoster
        style={{
          background: `url(${poster})`,
          backgroundSize: "cover",
          backgroundRepeat: "no-repeat",
        }}
        data-aos="fade"
        data-aos-duration="1200"
      >
        <div
          className="nominate"
          style={{ color: "#91AA9D", "padding-top": "50%" }}
        >
          <HeartFillIcon size={55} />
          <Paragraph className="text-center">Nominate</Paragraph>
        </div>
      </MoviePoster>
    );
  }

  renderBlankMoviePoster() {
    return (
      <MoviePoster
        style={{
          "background-color": "#1A3341",
        }}
        data-aos="fade"
        data-aos-duration="1200"
      ></MoviePoster>
    );
  }

  renderSelectedMoviePoster() {
    return (
      <MoviePoster
        style={{
          "background-color": "#1A3341",
        }}
        data-aos="fade"
        data-aos-duration="1200"
      ></MoviePoster>
    );
  }

  render() {
    const { title, poster, year } = this.state;
    let selected = false;

    return (
      <Styles>
        {poster !== "N/A" && (
          <a
            className="selectPoster"
            disabled={selected}
            onClick={() => {
              this.props.click(title, poster, year);
              selected = true;
            }}
          >
            {this.renderMoviePoster()}
          </a>
        )}

        {poster == "N/A" && (
          <a
            className="selectPoster"
            onClick={() => this.props.click(title, poster, year)}
          >
            {this.renderBlankMoviePoster()}
          </a>
        )}

        <Paragraph
          className="title"
          data-aos="fade"
          data-aos-duration="1200"
          data-aos-delay="150"
        >
          {title}

          <span
            className="year"
            data-aos="fade"
            data-aos-duration="1200"
            data-aos-delay="200"
          >
            {year}
          </span>
        </Paragraph>
      </Styles>
    );
  }
}

export default MovieResult;
